<script>
	export let xml;
	export let uxml;
	export let ansStatus;
	export let isReview;
	import { onMount, beforeUpdate } from 'svelte';
	import {
		XMLToJSON, 
		onUserAnsChange
	} from '../helper/HelperAI.svelte';
	import ItemHelper from '../helper/ItemHelper.svelte';
	import {
		movetarget,
	} from './libs/util.svelte';
	let parseXml = "";
	let bgImgPath = '//s3.amazonaws.com/jigyaasa_content_static/';
	let alt = "";
	let imgwidth = "auto";
	let imgheight = "auto";
	let itemBorder = 0;
	let itemBorderColor = "gray";
	let isUxmlTarget = false;
	let itemAreaTop = '';
	let itemAreaHeight = '';
	let itemAreaWidth = '';
	let itemAreaLeft = '';
	let targetLeft 		= 100;
	let targetTop 		= 100;
	let targetView      = "none";
	let ans_x 			= 0;
	let ans_y 			= 0;
	let ans_h 			= 0;
	let ans_w 			= 0;
	let type			= "";
	let img_url 		= "";
	let manual_grade 	= 0;
	let onError			= null; 
	let item_type       = "";
	let xmlHeight       = 0;
	let xmlWidth        = 0;

	onMount(async () => {
		parseXml = XMLToJSON(xml);
		xmlParser();
		preRender();
	});
	beforeUpdate(()=>{
		console.log("beforeUpdate");
	})

	function xmlParser() {
		item_type = parseXml['smxml']['div']['_type'];
		xmlHeight = parseXml['smxml']['_height'];
		xmlWidth = parseXml['smxml']['_width'];
		if (item_type == undefined || item_type == "") {
			item_type = parseXml['smxml']['_name'].toLowerCase();
		}
		img_url = parseXml['smxml']['_bgimg'];

		ans_y = parseFloat(parseXml['smxml']['div']['_top']);
		ans_x = parseFloat(parseXml['smxml']['div']['_left'])+13;
		ans_h = parseFloat(parseXml['smxml']['div']['_height']);
		ans_w = parseFloat(parseXml['smxml']['div']['_width']);
		alt = parseXml['smxml']['div']['_alt'];
		type = parseXml['smxml']['div']['type'];
		itemBorder  = parseXml.smxml.div._border;
		itemBorderColor  = parseXml.smxml.div._bordercolor;
		itemAreaWidth = parseXml.smxml.div._width+'px';
		itemAreaHeight = parseXml.smxml.div._height+'px';
		itemAreaLeft = parseXml.smxml.div._left+'px';
		itemAreaTop = parseXml.smxml.div._top+'px';
		
	}

	function preRender() {
		if (isReview) {
			targetView = "block";
		}
		var image = new Image();
		image.src = bgImgPath + img_url;
		image.onload = function() {
			imgheight =  (parseXml.smxml.div._imgheight)  ? parseXml.smxml.div._imgheight+'px' : "auto !important";
			imgwidth = (parseXml.smxml.div._imgwidth) ? parseXml.smxml.div._imgwidth+'px' :  "auto !important";
			console.log({imgwidth});
		};
		if (uxml) {
			let parseUxml = XMLToJSON(uxml);
			if (parseUxml.SMANS && parseUxml.SMANS.div) {
				isUxmlTarget = true;
				ans_x = parseUxml.SMANS.div._targetLeft;
				ans_y = parseUxml.SMANS.div._targetTop;
			}
		}
	}

	function checkAnswer (event) {
		let result = movetarget(event, ans_h, ans_w, parseInt(itemAreaLeft), parseInt(itemAreaTop));
		isUxmlTarget = true;
		ans_x = result.left;
		ans_y = result.top;
		ansStatus = result.ans;
		onUserAnsChange(result);
	}

	function onModalTouch(event) {
		console.log(event);
	}

	function setReview() {
		targetView = "block";
    }

    function unsetReview() {
        targetView = "none";
    }
	
</script>

<main>
	<ItemHelper 
		on:setReview = {setReview}
		on:unsetReview = {unsetReview}
	/>
	<table id="hptmain0" class='smbase smhotspot border-0 h-auto w-auto'>
		<tbody>
			<tr>
				<td class="border">
					<div id="SM0" class="relative">
						<div 
							id='SM0' 
							class="SM position-relative m-0 p-0" 
							style="{`
								position: relative;
								margin: 0px;
								padding: 0px;
								width: 100%;
								height: 100%;
								border: ${(itemBorder) ? itemBorder + 'px solid' : ''};
								borderColor: ${itemBorderColor};
							`}"
						>
							<img 
								id="im0" 
								tabindex="0" 
								style="{`max-width:none; width:${imgwidth}; height:${imgheight};`}" 
								class="hotSpotImg" 
								src={bgImgPath+img_url} 
								alt={alt} 
								on:click={checkAnswer}
							/>
							<div 
								id='hotArea' 
								class="hotArea hotArea hotAreaPreview" 
								style="{`
									display: ${targetView};
									left:${itemAreaLeft};
									top:${itemAreaTop};
									height:${itemAreaHeight};
									width:${itemAreaWidth};
								`}"
							>
								&nbsp;
							</div>
							<span
								id='target' 
								class="target targetImg icomoon-plus-circle-2"
								class:showBlock="{isUxmlTarget}"
								style = "{`
									left:${ans_x}px;
									top:${ans_y}px;
								`}"
							>
							</span>
						</div>
					</div>
				</td>
			</tr>
		</tbody>
	</table>
</main>

<style>
	main {
		text-align: center !important;
		padding: 1em;
		max-width: 240px;
		margin: 0 auto;
		font-size: 26px;
	}

	.targetImg {
		display : none;
		position: absolute;
		z-index: 10;
		width: 26px;
		height:26px;
		border-radius: 50%;
		background: #fff;
		color: #1c3ad4;
	}	

	.showBlock {
		display : block;
	}

	@media (min-width: 640px) {
		main {
			max-width: none;
		}
	}
</style>